import { createContext, useContext, useState } from "react"

const TodoContext = createContext();

export function useTodoContext() {
    return useContext(TodoContext);
}

export function TodoContextProvider({children}){
    const [ todoId, setTodoId ] = useState(4)

    

    const [ todoList, setTodoList ] = useState([
        {
            id: 0,
            date: "20220909",
            title: "",
            category: "",
            priority: "3",
            time: "9~",
            place: "",
            detail: "",
            isFavorited: false
        },
        {
            id: 1,
            date: "20220907",
            title: "",
            category: "",
            priority: "3",
            time: "9~",
            place: "",
            detail: "5km ",
            isFavorited: false
        },
        {
            id: 2,
            date: "20220909",
            title: "",
            category: "",
            priority: "2",
            time: "9~",
            place: "",
            detail: "5km 5",
            isFavorited: false
        },
        {   
            id: 3,
            date: "20220909",
            title: "",
            category: "",
            priority: "1",
            time: "9~",
            place: "",
            detail: "",
            isFavorited: false
        }, 
    ])

    const addTodo = (todo) => {
        setTodoList((prevState) => [...prevState, todo])
    }

    const changeTodo = (tentTodo) => {
        setTodoList(
            todoList.map((todo) => (todo.id === tentTodo.id ? tentTodo : todo))
        )
    }

    const changeFavo = (tentTodo) => {
        setTodoList(
            todoList.map((todo) => (todo.id === tentTodo.id) ? 
                {
                    id: todo.id,
                    date: todo.date,
                    title: todo.title,
                    category: todo.category,
                    priority: todo.priority,
                    time: todo.time,
                    place: todo.place,
                    detail: todo.detail,
                    isFavorited: !todo.isFavorited
                } : todo
            )
        )
    }

    const deleteTodo = (tentTodo) => {
        setTodoList((prevState) => prevState.filter((todo) => todo !== tentTodo))
    }

    const value = {
        todoId,
        setTodoId,
        todoList,
        addTodo,
        changeTodo,
        changeFavo,
        deleteTodo
    }

    return (
        <TodoContext.Provider value = {value}>{children}</TodoContext.Provider>
    )
}